@charset "UTF-8";
/*-----------------------------------------------------------------------------------*/
/* GENERAL STYLES
/*-----------------------------------------------------------------------------------*/
* {
  box-sizing: border-box;
}

body {
  margin: 0 !important;
  background-color: #111;
  font-size: 18px;
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Rajdhani", sans-serif !important;
  font-weight: 600;
  text-transform: uppercase;
}

.wrap {
  max-width: 1170px;
  width: 95%;
  margin: 0 auto;
}

.custom-logo {
  max-width: 130px;
  height: auto;
}

.btn-rosa {
  background-color: #dc143f;
  color: #fff;
  text-decoration: none;
  padding: 10px;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
}

.btn-rosa-cat {
  background-color: #dc143f;
  color: #fff;
  text-decoration: none;
  padding: 0px 5px;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
}

/*****************Estilos del sidebar de busqueda********************************************

/* Estilo para la etiqueta del campo de búsqueda */
.wp-block-search__label {
  font-size: 16px; /* Tamaño de fuente */
  color: #333; /* Color del texto */
  display: block; /* Mostrar como bloque */
  margin-bottom: 10px; /* Espaciado inferior */
}

/* Estilo para el contenedor interno */
.wp-block-search__inside-wrapper {
  display: flex; /* Flexbox para alinear elementos */
  align-items: center; /* Alinear elementos verticalmente */
  margin-bottom: 25px;
}

/* Estilo para el campo de entrada */
.wp-block-search__input {
  flex: 1; /* Ocupa el espacio disponible */
  padding: 10px; /* Espaciado interno */
  border: 1px solid #ccc; /* Borde del campo */
  border-radius: 5px; /* Bordes redondeados */
  font-size: 16px; /* Tamaño de fuente */
  margin-right: 10px; /* Espaciado a la derecha */
}

/* Estilo para el botón de búsqueda */
.wp-block-search__button {
  padding: 10px 20px; /* Espaciado interno */
  background-color: #0073aa; /* Color de fondo */
  color: #fff; /* Color del texto */
  border: none; /* Sin borde */
  border-radius: 5px; /* Bordes redondeados */
  font-size: 16px; /* Tamaño de fuente */
  cursor: pointer; /* Cambia el cursor al pasar sobre el botón */
  transition: background-color 0.3s; /* Transición suave para el color de fondo */
}

/* Estilo para el botón de búsqueda en hover */
.wp-block-search__button:hover {
  background-color: #005177; /* Color de fondo al pasar el ratón */
}

/*-----------------------------------------------------------------------------------*/
/* MOBILE STYLES
/*-----------------------------------------------------------------------------------*/
/*******************************************************************************/
/*********************IMPORTS COMPONENTS***********************/
/*******************************************************************************/
.wrap-hdr {
  padding: 5px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: solid 1px #fff;
}
.wrap-hdr .nav-hdr {
  display: none;
}
@media screen and (min-width: 769px) {
  .wrap-hdr {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-items: center;
  }
  .wrap-hdr .nav-hdr {
    display: block;
  }
  .wrap-hdr .nav-hdr ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    gap: 20px;
  }
  .wrap-hdr .nav-hdr ul li a {
    text-decoration: none;
    color: #fff;
    font-family: "Rajdhani", sans-serif;
    font-weight: 500;
    font-size: 15px;
    text-transform: uppercase;
    padding: 10px 20px;
  }
  .wrap-hdr .nav-hdr ul li a:hover {
    color: #dc143f;
  }
}

.social {
  display: flex;
  list-style: none;
  padding: 0;
  flex-direction: row;
  gap: 10px;
}
.social li a {
  width: 30px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  border: solid 1px #fff;
  border-radius: 50%;
  color: #fff;
  text-decoration: none;
  font-size: 16px;
}
.social li a:hover {
  color: #dc143f;
}

.wrap-menu {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}
.wrap-menu .toggle {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
}
.wrap-menu .menu {
  background-color: #161616;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease-in-out;
  z-index: 1;
}
.wrap-menu .menu .close {
  padding: 10px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border: solid 1px #fff;
  border-radius: 50%;
  cursor: pointer;
  margin: 30px;
}
.wrap-menu .menu nav {
  height: 100%;
  z-index: 10;
}
.wrap-menu .menu nav .menu-menu-1-container {
  height: 100%;
}
.wrap-menu .menu nav .menu-menu-1-container ul {
  list-style: none;
  padding: 0;
  height: 100%;
}
.wrap-menu .menu nav .menu-menu-1-container ul li {
  text-align: center;
  display: block;
}
.wrap-menu .menu nav .menu-menu-1-container ul li a {
  color: #fff;
  padding: 10px 15px;
  display: block;
  text-decoration: none;
  font-family: "Rajdhani", sans-serif;
  font-size: 15px;
  text-transform: uppercase;
}
.wrap-menu .menu-active {
  opacity: 1;
  height: 100%;
  transition: overflow 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
@media screen and (min-width: 992px) {
  .wrap-menu .toggle {
    display: none;
  }
  .wrap-menu .menu {
    display: block;
    position: relative;
    background-color: transparent;
    opacity: 1;
    height: auto;
  }
  .wrap-menu .menu .close {
    display: none;
  }
  .wrap-menu .menu nav {
    height: 100%;
  }
  .wrap-menu .menu nav .menu-menu-1-container {
    height: 100%;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul {
    display: flex;
    justify-content: space-evenly;
    margin: 0;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul li a {
    color: #fff;
    padding: 10px;
    display: block;
    font-weight: 500;
  }
  .wrap-menu .menu nav .menu-menu-1-container ul li a:hover {
    color: #dc143f;
  }
}

.comp-footer {
  display: grid;
  gap: 20px;
}
.comp-footer .item-wrap {
  min-height: 300px;
  background-color: #161616;
  padding: 30px;
}
.comp-footer .item-wrap .btn-hdr {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.comp-footer .item-wrap .btn-hdr h3 {
  font-size: 13px;
}
.comp-footer .item-wrap ul {
  list-style: none;
  padding: 0;
}
.comp-footer .item-wrap ul li a {
  color: #adadad;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
}
.comp-footer .item-wrap ul li a:hover {
  color: #dc143f;
}
.comp-footer .item-wrap .footer-list li {
  border-bottom: 1px solid rgba(173, 173, 173, 0.2392156863);
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.comp-footer .item-wrap .footer-list li:nth-of-type(3) {
  border-bottom: 0px;
}
.comp-footer .item-wrap:nth-of-type(2) {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.comp-footer .item-wrap:nth-of-type(2) p {
  text-align: center;
  color: #adadad;
}
@media screen and (min-width: 769px) {
  .comp-footer {
    grid-template-columns: 2fr 3fr 2fr;
  }
}

.grid-principal {
  display: grid;
  gap: 20px;
}
.grid-principal .item-grid-prin {
  height: 250px;
  overflow: hidden;
  position: relative;
}
.grid-principal .item-grid-prin img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.grid-principal .item-grid-prin div {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.484);
  padding: 10px;
}
.grid-principal .item-grid-prin div span {
  padding: 0;
  font-size: 12px;
  color: #d6d6d6;
}
.grid-principal .item-grid-prin div h2 {
  margin: 0;
  font-size: 20px;
  color: #fff;
  font-weight: bold;
}
.grid-principal .item-grid-prin div h2:hover {
  color: #dc143f;
}
.grid-principal .item-grid-prin div ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #fff;
}
.grid-principal .item-grid-prin div ul li {
  font-size: 16px;
  color: #999;
}
.grid-principal .item-grid-prin div ul li span {
  color: #fff;
}
.grid-principal .item-grid-prin:hover > .item-hov-zoom {
  transform: scale(1.1);
  transition: transform 0.4s ease-in-out;
}
@media screen and (min-width: 769px) {
  .grid-principal {
    grid-template-columns: 1fr 1fr;
  }
  .grid-principal .item-grid-prin {
    height: 350px;
  }
  .grid-principal .item-grid-prin div span {
    font-size: 15px;
  }
  .grid-principal .item-grid-prin div h2 {
    font-size: 28px;
  }
  .grid-principal .item-grid-prin div ul li {
    font-size: 15px;
  }
  .grid-principal .item-grid-prin:nth-of-type(1) {
    height: 550px;
    grid-column: 1/3;
  }
}

.img-single-post {
  height: 500px;
  overflow: hidden;
  position: relative;
}
.img-single-post img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.img-single-post .title-single-post {
  height: 500px;
  width: 100%;
  background-color: rgba(50, 98, 161, 0.2901960784);
  position: absolute;
  top: 0;
  left: 0;
}
.img-single-post .title-single-post .flex-title {
  display: flex;
  align-items: end;
  padding-bottom: 80px;
}
.img-single-post .title-single-post .flex-title h1 {
  max-width: 900px;
  color: #fff;
  font-weight: 600;
  text-shadow: 1px 1px 8px black;
}

/* Estilo para el contenedor de navegación */
.post-navigation {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  padding: 10px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}

/* Estilo para el enlace de la entrada anterior */
.post-navigation__prev a {
  display: inline-block;
  padding: 10px 20px;
  background-color: var(--e-global-color-62e34c4);
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

/* Estilo para el enlace de la entrada siguiente */
.post-navigation__next a {
  display: inline-block;
  padding: 10px 20px;
  background-color: var(--e-global-color-62e34c4);
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

/* Estilo para el enlace en hover */
.post-navigation__prev a:hover,
.post-navigation__next a:hover {
  background-color: var(--e-global-color-62e34c4);
}

